<template>
  <div>
    <b-container fluid class="container_b">
      <b-row>
        <div class="container">
          <ul class="progressbar">
            <li :class="{active: moodActive}">CHOOSE MOOD</li>
            <li :class="{active: tagsActive}">SELECT TAGS</li>
            <li :class="{active: geoActive}">LOCATE ME</li>
            <li :class="{active: subActive}">SUBMIT</li>
          </ul>
        </div>
      </b-row>
      <b-row>
        <transition name="fade" mode="out-in">
          <div key="one" v-if="!form.mood" class="form-item">
            <div class="form-item-heading">
              <h2>What is your current mood?</h2>
            </div>
            <br />
            <span>
              <p>Select one of the five options below corresponding to your current mood</p>
            </span>
            <div class="form-item-main">
              <div
                v-if="selected"
                class="face"
                :style="{backgroundImage: `url(${faceBckrnd})`, borderColor: `${faceColor}`}"
              ></div>
              <b-form-radio-group
                class="radio-helper"
                v-model="selected"
                :options="options"
                name="radios-stacked"
                value-field="mood"
                text-field="mood"
                stacked
              ></b-form-radio-group>
              <div class="error" :style="{visibility: `${hideMe}`}">*Please select your mood</div>
            </div>
            <div class="submit-section">
              <b-button @click="submitMood()" variant="secondary">Next</b-button>
            </div>
          </div>
          <div key="two" v-else-if="form.tags.length < 1" class="form-item">
            <div class="form-item-heading">
              <h2>Describe your mood</h2>
            </div>
            <br />
            <span>
              <p>Insert from one to three words, in the form below, that describes your mood</p>
            </span>
            <div class="form-item-main">
              <b-form-group :state="state">
                <b-form-tags
                  input-id="tags-validation"
                  :input-attrs="{ 'aria-describedby': 'tags-validation-help' }"
                  v-model="tags"
                  :state="state"
                  :tag-validator="tagValidator"
                  separator=" "
                ></b-form-tags>
                <!-- The following slots are for b-form-group -->
                <template v-slot:invalid-feedback>You must provide at least 1 tag and no more than 3</template>
                <template v-slot:description>
                  <div id="tags-validation-help">
                    Tags must be 2 to 15 characters in length and all lower
                    case. Enter tags separated by spaces or press enter.
                  </div>
                </template>
              </b-form-group>
            </div>
            <div class="submit-section">
              <b-button @click="submitTags()" variant="secondary">Next</b-button>
            </div>
          </div>
          <div key="three" v-else-if="form.location.length < 1" class="form-item">
            <div class="form-item-heading">
              <h2>Where are you?</h2>
            </div>
            <br />
            <span>
              <p>Click in the button below to enable your geolocation and let us track your position</p>
            </span>
            <div class="form-item-main">
              <div class="geoicon">
                <b-icon class="geoicon-helper" :style="{fill: `${iconColor}`}" icon="geo"></b-icon>
              </div>
              <div class="button-helper">
                <b-button
                  v-if="!isLoading"
                  @click="getlocation()"
                  pill
                  variant="outline-secondary"
                >Locate me</b-button>
                <b-button v-if="isLoading" pill variant="outline-secondary" disabled>
                  <span class="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
                  Locating..
                </b-button>
              </div>
              <div
                v-if="!geolocationObj.errorMsg"
                :style="{visibility: `${hideMegeo}`}"
                class="geoOk"
              >
                <p
                  class="geoOktext"
                >Latitude: {{geolocationObj.lat}}, Longitude: {{geolocationObj.lon}}. Click next to proceed.</p>
              </div>
              <div v-else :style="{visibility: `${hideMegeo}`}" class="geoOk">
                <p class="geoOktextErr">{{geolocationObj.errorMsg}}</p>
              </div>
            </div>
            <div class="submit-section">
              <b-button @click="submitGeo()" variant="secondary">Next</b-button>
            </div>
          </div>
          <div key="four" v-else class="form-item">
            <div class="form-item-heading">
              <h2>We are Done!</h2>
            </div>
            <div class="form-item-main">
              <transition name="loading" mode="out-in">
                <div key="one" v-if="!serverStatus" class="d-flex align-items-center">
                  <span>Submitting your data. Please wait..</span>
                  <div class="spinner-border ml-auto" role="status" aria-hidden="true"></div>
                </div>
                <div key="two" v-else-if="serverStatus = 200">
                  <span class="help">Your data has been submitted</span>
                  <b-icon class="icon-big" icon="check-all"></b-icon>
                </div>
                <div key="three" v-else-if="serverStatus = 404 || 500">
                  <span class="help-er">Error! Please try again later.</span>
                  <b-icon class="icon-big-er" icon="exclamation-triangle-fill"></b-icon>
                </div>
              </transition>
            </div>
          </div>
        </transition>
      </b-row>
    </b-container>
  </div>
</template>

<script>
/* eslint-disable */
export default {
  name: "Form",
  data() {
    return {
      selected: "",
      options: [
        { mood: "Very Sad", img: "unhappy.svg", color: "#ED1613" },
        { mood: "Somewhat Sad", img: "mostly_unhappy.svg", color: "#E38A04" },
        { mood: "Neutral", img: "normal.svg", color: "#636363" },
        { mood: "Somewhat Happy", img: "mostly_happy.svg", color: "#87B1B2" },
        { mood: "Very Happy", img: "happy.svg", color: "#008728" }
      ],
      faceColor: "",
      faceBckrnd: "",
      iconColor: "",
      form: { mood: "", tags: [], location: [] },
      hideMe: "hidden",
      hideMegeo: "hidden",
      errorMood: false,
      moodActive: false,
      tags: [],
      dirty: false,
      tagsActive: false,
      geoActive: false,
      isLoading: false,
      geolocationObj: {
        lon: 0,
        lat: 0,
        errorMsg: ""
      },
      serverStatus: null,
      subActive: false
    };
  },
  methods: {
    submitMood() {
      if (!this.selected) {
        this.hideMe = "visible";
        this.errorMood = true;
      } else {
        this.form.mood = this.selected;
        this.moodActive = true;
        this.errorMood = false;
      }
    },
    tagValidator(tag) {
      // Individual tag validator function
      return (
        tag === tag.toLowerCase() &&
        tag.length > 1 &&
        tag.length < 16 &&
        isNaN(tag)
      );
    },
    submitTags() {
      if (this.state) {
        this.tagsActive = true;
        this.tags.forEach(el => {
          this.form.tags.push(el);
        });
      }
    },
    getlocation() {
      this.iconColor = "";
      this.isLoading = true;

      let options = {
        enableHighAccuracy: true,
        timeout: 30000,
        maximumAge: 0
      };

      const geolocation = new Promise((resolve, reject) => {
        navigator.geolocation.getCurrentPosition(
          position => {
            resolve(position.coords);
            this.geolocationObj.lon = position.coords.longitude.toFixed(5);
            this.geolocationObj.lat = position.coords.latitude.toFixed(5);
            this.isLoading = false;
            this.iconColor = "#007bff";
            this.hideMegeo = "visible";
          },
          error => {
            reject(error);
            this.geolocationObj,
              (errorMsg =
                "Something went wrong, please enable geolocation and try again");
            this.hideMegeo = "visible";
          },
          options
        );
      }).catch(error => error);
    },
    submitGeo() {
      if (this.geolocationObj.lat) {
        this.form.location.splice(0, 0, parseFloat(this.geolocationObj.lon));
        this.form.location.splice(1, 0, parseFloat(this.geolocationObj.lat));
        this.geoActive = true;
        this.subActive = true;
      }

      if (this.form.mood && this.form.tags && this.form.location) {
        this.axios
          .post("https://morning-forest-91172.herokuapp.com/form", this.form)
          .then(res => {
            this.serverStatus = res.status;
          })
          .catch(err => {
            this.serverStatus = err.response.status;
          });
      }
    }
  },
  computed: {
    state() {
      // Overall component validation state
      return this.dirty ? this.tags.length > 0 && this.tags.length < 4 : null;
    }
  },
  watch: {
    selected(val) {
      this.options.map(el => {
        if (el.mood == val) {
          this.faceColor = el.color;
          this.faceBckrnd = el.img;
          console.log(el.mood);
        }
      });
    },
    tags(newVal, oldVal) {
      // Set the dirty flag on first change to the tags array
      this.dirty = true;
    }
  },
  updated() {}
};
</script>

<style>
@import url("https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap");

body {
  font-family: "Roboto";
}

.container_b {
  max-width: 100%;
}

.form-item {
  width: 900px;
  height: auto;
  background-color: rgba(220, 220, 220, 0.8);
  margin: auto;
  margin-bottom: 100px;
  padding: 40px;
  color: #7d7d7d;
}

.form-item-heading {
  position: relative;
  font-weight: 700;
  width: 90%;
  border-bottom: 1px solid #7d7d7d;
  margin-bottom: 5px;
}

.form-item-main {
  position: relative;
  width: 70%;
  height: auto;
  margin-top: 50px;
  margin-left: auto;
  margin-right: auto;
}

.radio-helper {
  border-right: 2px dotted #7d7d7d;
  width: 50%;
}

.icon-big {
  position: relative;
  width: 80px;
  height: 80px;
  fill: #007bff;
  display: inline;
  margin-left: 80%;
}

.icon-big-er {
  position: relative;
  width: 80px;
  height: 80px;
  fill: red;
  display: inline;
  margin-left: 80%;
}

.help {
  position: absolute;
  display: inline;
  height: 80px;
  top: 32px;
  color: black;
}

.help-er {
  position: absolute;
  display: inline;
  height: 80px;
  top: 32px;
  color: red;
}

.button-helper {
  position: relative;
  width: 50%;
  border-right: 2px dotted #7d7d7d;
  margin-top: 15px;
}

.face {
  position: relative;
  width: 80px;
  height: 80px;
  background-position-x: 50%;
  background-position-y: 50%;
  background-size: 120%;
  float: right;
  margin-top: 22px;
  border-radius: 50%;
  margin-right: 15%;
  border: 2px solid;
}

.geoicon {
  position: relative;
  width: 70px;
  height: 110px;
  float: right;
  margin-right: 15%;
  padding-bottom: 20px;
}

.geoicon-helper {
  width: 50px;
  height: 50px;
  position: absolute;
  top: -6px;
}

.error {
  margin-top: 30px;
  margin-left: 30px;
  color: red;
  font-size: small;
}

.geoOk {
  margin-top: 55px;
  margin-left: 0px;
}

.geoOktext {
  color: #007bff;
  font-size: small;
}

.geoOktextErr {
  color: red;
  font-size: small;
}

.submit-section {
  position: relative;
  width: 90%;
  height: auto;
  padding-left: 80%;
  padding-top: 20px;
  margin-left: auto;
  margin-right: auto;
}

.container {
  width: 800px;
  margin: 100px auto;
}
.progressbar {
  margin: 0;
  padding: 0;
  counter-reset: step;
}
.progressbar li {
  list-style-type: none;
  width: 25%;
  float: left;
  font-size: 12px;
  position: relative;
  text-align: center;
  text-transform: uppercase;
  color: #7d7d7d;
}
.progressbar li:before {
  width: 30px;
  height: 30px;
  content: counter(step);
  counter-increment: step;
  line-height: 30px;
  border: 2px solid #7d7d7d;
  display: block;
  text-align: center;
  margin: 0 auto 10px auto;
  border-radius: 50%;
  background-color: white;
}
.progressbar li:after {
  width: 100%;
  height: 2px;
  content: "";
  position: absolute;
  background-color: #7d7d7d;
  top: 15px;
  left: -50%;
  z-index: -1;
}
.progressbar li:first-child:after {
  content: none;
}
.progressbar li.active {
  color: #007bff;
}
.progressbar li.active:before {
  border-color: #007bff;
}
.progressbar li.active + li:after {
  background-color: #007bff;
}

.fade-enter-active {
  transition: all 0.3s ease;
}

.fade-leave-active {
  transition: all 0.3s ease;
}

.fade-enter,
.fade-leave-to {
  opacity: 0;
}

.loading-enter-active {
  transition: all 0.3s ease;
}

.loading-leave-active {
  transition: all 0.3s ease;
}

.loading-enter,
.loading-leave-to {
  opacity: 0;
}

h2 {
  display: inline;
  font-weight: 700;
}

p {
  color: black;
}
</style>