<template>
  <div id="app">
    <Form />
    <Map :countriesCoords="countriesData" :citiesCoords="citiesData" :votesCoords="votesData" />
  </div>
</template>

<script>
/* eslint-disable */

import Map from "./components/Map.vue";
import Form from "./components/Form.vue";

export default {
  name: "app",
  components: {
    Map,
    Form
  },
  data() {
    return {
      countriesData: [],
      citiesData: [],
      votesData: []
    };
  },
  methods: {
    importCountries() {
      this.axios
        .get("https://morning-forest-91172.herokuapp.com/countries")
        .then(res => {
          return res;
        })
        .then(onres => {
          this.countriesData = onres.data;
        })
        .catch(err => {
          throw new Error(err);
        });
    },
    importCities() {
      this.axios
        .get("https://morning-forest-91172.herokuapp.com/cities")
        .then(res => {
          return res;
        })
        .then(onres => {
          this.citiesData = onres.data;
        })
        .catch(err => {
          throw new Error(err);
        });
    },
    importVotes() {
      this.axios
        .get("https://morning-forest-91172.herokuapp.com/votes")
        .then(res => {
          return res;
        })
        .then(onres => {
          this.votesData = onres.data;
        })
        .catch(err => {
          throw new Error(err);
        });
    }
  },
  mounted() {
    this.importCountries();
    this.importCities();
    this.importVotes();
  }
};
</script>

