<template>
  <div id="map">
    <div class="sidebar_container">
      <div class="legend_icon">
        <b-button title="Legend" @click="toggleLegend" class="legend_button" variant="light">
          <b-icon icon="info-circle" scale="1.2"></b-icon>
        </b-button>
      </div>
      <div class="legend_icon">
        <b-button title="Statistics" class="legend_button" variant="light">
          <b-icon icon="bar-chart" scale="1.2"></b-icon>
        </b-button>
      </div>
      <div class="legend_icon">
        <b-button title="Choropleth Map" class="legend_button" variant="light">
          <b-icon icon="map" scale="1.2"></b-icon>
        </b-button>
      </div>
      <div class="legend_icon">
        <b-button title="Zoom Out" class="legend_button" variant="light">
          <b-icon icon="box-arrow-up-left" scale="1.2"></b-icon>
        </b-button>
      </div>
    </div>
    <div class="sidebar_container_right" :class="{sidebar_container_right_in: showLegend}">
      <div class="legend_title">Legend</div>
      <div class="legend_desc">MOODS (DESCRIPTION)</div>
      <div class="legend_item">
        <div class="color_item" :style="{backgroundColor: `${options[4].color}`}"></div>
        <div class="desc_item">{{options[4].mood}}</div>
      </div>
      <div class="legend_item">
        <div class="color_item" :style="{backgroundColor: `${options[3].color}`}"></div>
        <div class="desc_item">{{options[3].mood}}</div>
      </div>
      <div class="legend_item">
        <div class="color_item" :style="{backgroundColor: `${options[2].color}`}"></div>
        <div class="desc_item">{{options[2].mood}}</div>
      </div>
      <div class="legend_item">
        <div class="color_item" :style="{backgroundColor: `${options[1].color}`}"></div>
        <div class="desc_item">{{options[1].mood}}</div>
      </div>
      <div class="legend_item">
        <div class="color_item" :style="{backgroundColor: `${options[0].color}`}"></div>
        <div class="desc_item">{{options[0].mood}}</div>
      </div>
    </div>
    <svg :width="widthNew" :height="height" ref="mapid">
      <g ref="countriesID">
        <g ref="rectID" />
        <g ref="countID" />
        <g ref="votesID" />
        <g ref="citiesID" />
      </g>
    </svg>
    <transition name="fade">
      <div id="loader" v-if="loader"></div>
    </transition>
  </div>
</template>

<script>
/* eslint-disable */

import * as d3 from "d3";
import * as topojson from "topojson-client";

export default {
  name: "Map",
  props: ["countriesCoords", "citiesCoords", "votesCoords"],
  data() {
    return {
      width: 0,
      widthNew: "100%",
      height: 800,
      scaleIni: {
        x: null,
        y: null,
        k: 1
      },
      selection: null,
      bounds: null,
      isRect: null,
      scaleOut: null,
      loader: true,
      options: [
        { mood: "Very Sad", color: "#ED1613" },
        { mood: "Somewhat Sad", color: "#E38A04" },
        { mood: "Neutral", color: "#636363" },
        { mood: "Somewhat Happy", color: "#87B1B2" },
        { mood: "Very Happy", color: "#008728" }
      ],
      showLegend: false,
      votesRadius: 3
    };
  },
  methods: {
    zoomMap() {
      const cntr = this.$refs.countriesID;
      const map = this.$refs.mapid;
      const gnew = this.$refs.countID;
      const vm = this;

      d3.select(map)
        .call(
          d3
            .zoom()
            .translateExtent([
              [-18000, -18000],
              [13000, 12000]
            ])
            .scaleExtent([0.1, 60])
            .on("zoom", function(d) {
              d3.select(cntr)
                .attr("transform", d3.event.transform)
                .attr("stroke-width", 1 / d3.event.transform.k + "px");

              console.log(d3.event.transform.k);

              vm.scaleIni = d3.event.transform;

              // vm.translatex =
              //  (vm.width/2 - vm.mousePos[0]) - d3.event.transform.x * d3.event.transform.k;
              // vm.translatey =
              //   (vm.height/2 - vm.mousePos[1]) - d3.event.transform.y * d3.event.transform.k;
              // vm.scale = d3.event.transform.k;

              // d3.select(cntr)
              //   .selectAll("circle")
              //   .attr("r", 2 / d3.event.transform.k);

              d3.select(cntr)
                .selectAll("text")
                .style("font-size", function(d) {
                  if (d.properties.POP_RANK <= 2) {
                    return 17 / Math.min(vm.scaleIni.k, 1.2) + "px";
                  } else if (d.properties.POP_RANK <= 3) {
                    return 13.5 / Math.min(vm.scaleIni.k, 1.8) + "px";
                  } else if (d.properties.POP_RANK <= 4) {
                    return 11 / Math.min(vm.scaleIni.k, 1.6) + "px";
                  } else {
                    return 10 / Math.min(vm.scaleIni.k, 1.6) + "px";
                  }
                })
                .style("font-weight", function(d) {
                  if (d.properties.POP_RANK <= 2) {
                    return 700;
                  }
                })
                .transition()
                .duration(100)
                .ease(d3.easeLinear)
                .style("opacity", function(d) {
                  if (vm.scaleIni.k < 0.5 && d.properties.POP_RANK < 2) {
                    return 1;
                  } else if (
                    vm.scaleIni.k >= 0.5 &&
                    vm.scaleIni.k <= 2 &&
                    d.properties.POP_RANK <= 2
                  ) {
                    return 1;
                  } else if (
                    vm.scaleIni.k > 1 &&
                    vm.scaleIni.k < 15 &&
                    d.properties.POP_RANK <= 4
                  ) {
                    return 1;
                  } else if (
                    vm.scaleIni.k > 1.5 &&
                    vm.scaleIni.k < 20 &&
                    d.properties.POP_RANK <= 5
                  ) {
                    return 1;
                  } else if (
                    vm.scaleIni.k > 3 &&
                    vm.scaleIni.k < 20 &&
                    d.properties.POP_RANK <= 6
                  ) {
                  } else {
                    return 0;
                  }
                });

              d3.select(cntr)
                .selectAll("circle")
                .attr("r", function() {
                  return vm.votesRadius / vm.scaleIni.k;
                });
            })
        )
        .on("dblclick.zoom", null)
        .on("click.zoom", function() {
          if (d3.select(vm.selection).classed("active")) {
            console.log("hi");
            return;
          } else if (d3.select(vm.selection).classed("rect")) {
            d3.selectAll(".feature")
              .classed("active", false)
              .transition()
              .duration(100)
              .attr("opacity", 1);
          } else {
            d3.select(gnew)
              .selectAll(".feature")
              .classed("active", false)
              .attr("opacity", 0.5);

            d3.select(vm.selection).classed("active", true);

            const dx = vm.bounds[1][0] - vm.bounds[0][0];
            const dy = vm.bounds[1][1] - vm.bounds[0][1];
            const x = (vm.bounds[0][0] + vm.bounds[1][0]) / 2;
            const y = (vm.bounds[0][1] + vm.bounds[1][1]) / 2;
            const scale = 0.9 / Math.max(dx / vm.width, dy / vm.height);

            const trX = vm.width / 2 - scale * x;
            const trY = vm.height / 2 - scale * y;

            vm.scaleIni.x = trX;
            vm.scaleIni.y = trY;
            vm.scaleIni.k = scale;

            d3.select(cntr)
              .transition()
              .duration(750)
              .attr("transform", `${vm.scaleIni}`)
              .attr("stroke-width", 1 / vm.scaleIni.k + "px");

            d3.select(cntr)
              .selectAll("text")
              .style("font-size", function(d) {
                if (d.properties.POP_RANK <= 2) {
                  return 17 / Math.min(vm.scaleIni.k, 1.2) + "px";
                } else if (d.properties.POP_RANK <= 3) {
                  return 13.5 / Math.min(vm.scaleIni.k, 1.8) + "px";
                } else if (d.properties.POP_RANK <= 4) {
                  return 11 / Math.min(vm.scaleIni.k, 1.6) + "px";
                } else {
                  return 10 / Math.min(vm.scaleIni.k, 1.6) + "px";
                }
              })
              .style("font-weight", function(d) {
                if (d.properties.POP_RANK <= 2) {
                  return 700;
                }
              })
              .transition()
              .duration(100)
              .ease(d3.easeLinear)
              .style("opacity", function(d) {
                if (vm.scaleIni.k < 0.5 && d.properties.POP_RANK < 2) {
                  return 1;
                } else if (
                  vm.scaleIni.k >= 0.5 &&
                  vm.scaleIni.k <= 2 &&
                  d.properties.POP_RANK <= 2
                ) {
                  return 1;
                } else if (
                  vm.scaleIni.k > 1 &&
                  vm.scaleIni.k < 15 &&
                  d.properties.POP_RANK <= 4
                ) {
                  return 1;
                } else if (
                  vm.scaleIni.k > 1.5 &&
                  vm.scaleIni.k < 20 &&
                  d.properties.POP_RANK <= 5
                ) {
                  return 1;
                } else if (
                  vm.scaleIni.k > 3 &&
                  vm.scaleIni.k < 20 &&
                  d.properties.POP_RANK <= 6
                ) {
                } else {
                  return 0;
                }
              });

            console.log("ho");
          }

          d3.select(cntr)
            .selectAll("circle")
            .attr("r", function() {
              return vm.votesRadius / vm.scaleIni.k;
            });
        });
    },
    toggleLegend() {
      this.showLegend = !this.showLegend;
    }
  },
  computed: {
    project() {
      let prj = d3
        .geoMercator()
        .center([20, 40])
        .scale(5000);
      return prj;
    }
  },
  watch: {
    countriesCoords(val) {
      const countries = topojson.feature(val, val.objects.data).features;
      const vm = this;
      const svg = this.$refs.countriesID;
      const gnew = this.$refs.countID;
      const rectG = this.$refs.rectID;

      const path = d3.geoPath().projection(this.project);

      d3.select(gnew)
        .selectAll("path")
        .data(countries)
        .enter()
        .append("path")
        .attr("d", path)
        .attr("class", "feature")
        .attr("pointer-events", "all")
        .on("click", function(d) {
          vm.selection = this;
          vm.bounds = path.bounds(d);

          // if (d3.select(this).classed("active")) {
          //   return;
          // } else {
          //   d3.selectAll(".feature")
          //     .classed("active", false)
          //     .attr("opacity", 0.5);

          //   d3.select(this).classed("active", true);

          //   const bounds = path.bounds(d);
          //   const dx = bounds[1][0] - bounds[0][0];
          //   const dy = bounds[1][1] - bounds[0][1];
          //   const x = (bounds[0][0] + bounds[1][0]) / 2;
          //   const y = (bounds[0][1] + bounds[1][1]) / 2;
          //   const scale = 0.9 / Math.max(dx / vm.width, dy / vm.height);

          //   const trX = vm.width / 2 - scale * x;
          //   const trY = vm.height / 2 - scale * y;

          //   d3.select(svg)
          //     .transition()
          //     .duration(750)
          //     .attr("transform", `translate(${trX}, ${trY}) scale (${scale})`)
          //     .attr("stroke-width", 1 / scale + "px");
          // }
        });

      d3.select(rectG)
        .append("rect")
        .attr("x", -16973.291015625)
        .attr("y", -10409.244140625)
        .attr("width", 31415.921875)
        .attr("height", 30181.755859375)
        .attr("class", "rect")
        .on("click", function() {
          vm.selection = this;

          // d3.selectAll(".feature")
          //   .classed("active", false)
          //   .attr("opacity", 1);
          // d3.select(svg)
          //   .transition()
          //   .duration(750)
          //   .attr("transform", `${vm.scaleIni}`)
          //   .attr("stroke-width", 1 / vm.scaleIni.k + "px");
        });

      console.log("redraw countries");
      this.loader = false;
    },
    citiesCoords(val) {
      const cities = topojson.feature(val, val.objects.data).features;
      const vm = this;
      const svg = this.$refs.countriesID;
      const gnewCities = this.$refs.citiesID;

      // d3.select(gnewCities)
      //   .selectAll("circle")
      //   .data(cities)
      //   .enter()
      //   .append("circle")
      //   .attr("cx", function(d) {
      //     return vm.project([
      //       d.geometry.coordinates[0],
      //       d.geometry.coordinates[1]
      //     ])[0];
      //   })
      //   .attr("cy", function(d) {
      //     return vm.project([
      //       d.geometry.coordinates[0],
      //       d.geometry.coordinates[1]
      //     ])[1];
      //   })
      //   .attr("r", 2)
      //   .style("fill", "red");

      d3.select(gnewCities)
        .selectAll("text")
        .data(cities)
        .enter()
        .append("svg:text")
        .text(function(d) {
          return d.properties.CITY_NAME;
        })
        .attr("transform", function(d) {
          return "translate(" + vm.project(d.geometry.coordinates) + ")";
        })
        .attr("class", function(d) {
          if (d.properties.POP_RANK <= 2) {
            return "text-map-main";
          } else {
            return "text-map";
          }
        })
        .style("opacity", function(d) {
          if (d.properties.POP_RANK < 2) {
            return 1;
          } else {
            return 0;
          }
        });

      console.log("draw cities");
    },
    votesCoords(val) {
      const votes = topojson.feature(val, val.objects.data).features;
      const vm = this;

      const gVotes = this.$refs.votesID;

      d3.select(gVotes)
        .selectAll("circle")
        .data(votes)
        .enter()
        .append("circle")
        .attr("cx", function(d) {
          return vm.project([
            d.geometry.coordinates[0],
            d.geometry.coordinates[1]
          ])[0];
        })
        .attr("cy", function(d) {
          return vm.project([
            d.geometry.coordinates[0],
            d.geometry.coordinates[1]
          ])[1];
        })
        .attr("r", vm.votesRadius)
        .style("fill", function(d) {
          switch (d.properties.mood) {
            case "Very Happy":
              return "#008728";
              break;
            case "Somewhat Happy":
              return "#87B1B2";
              break;
            case "Neutral":
              return "#636363";
              break;
            case "Somewhat Sad":
              return "#E38A04";
              break;
            case "Very Sad":
              return "#ED1613";
              break;
          }
        })
        .on("mouseover", function(d) {
          let x = d3.event.clientX + 10;
          let y = d3.event.clientY - 200;

          d3.select("#map")
            .append("div")
            .attr("id", "tooltip")
            .style("position", "absolute")
            .style("top", y + "px")
            .style("left", x + "px")
            .style("border", "1px solid white")
            .style("background-color", "#485566")
            .style("border-radius", 2.5 + "%")
            .style("padding", 10 + "px")
            .style("box-shadow", "3px 3px 2px 1px black")
            .style("opacity", 0.8)
            .html(
              `<b style="color: white;">${d.properties.mood}</b><br><strong style="color: #baba45;">{${d.properties.tags}}</strong><br><small style="font-weight: 0.2vw; color: white;">Voted at: ${d.properties.date} - ${d.properties.country.name}</small>`
            );
        })
        .on("mouseleave", function() {
          d3.select("#tooltip").remove();
        });

      console.log("Draw votes");
    }
  },
  created() {},
  mounted() {
    this.zoomMap();
    this.width = window.innerWidth;
  },
  updated() {}
};
</script>

<style>
@import url("https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap");

body {
  font-family: "Roboto";
}

#map {
  overflow: hidden;
  width: 100%;
  background-color: #000f1a;
  cursor: pointer;
  position: relative;
}

#loader {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 800px;
  z-index: +100;
  background: #000f1a url("/loader.gif") no-repeat center center;
}

.text-map {
  font-family: "Roboto";
  font-weight: 700;
  stroke: black;
  fill: #b0b0b0;
  pointer-events: none;
  -webkit-text-stroke: 3px;
}

.text-map-main {
  font-family: "Roboto";
  font-weight: 700;
  stroke: black;
  fill: #b58fd2;
  pointer-events: none;
  -webkit-text-stroke: 3px;
}

.move {
  cursor: move;
}

.feature {
  stroke: #4b4255;
  fill: #2a2a28;
}

.active {
  opacity: 1;
}

rect {
  pointer-events: visibleFill;
  fill: #000f1a;
  opacity: 1;
}

.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}

.sidebar_container {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 80px;
  height: 800px;
  z-index: +100;
  background-color: #404040;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  box-shadow: 1px 3px 5px 5px black;
  opacity: 0.8;
  cursor: default;
}

.legend_icon {
  order: 1;
  width: 60px;
  height: 60px;
  margin: 10px;
}

.legend_button {
  width: 100%;
  height: 100%;
  opacity: 0.9;
}

.sidebar_container_right {
  position: absolute;
  top: 0px;
  right: -350px;
  width: 300px;
  height: 800px;
  z-index: +100;
  background-color: #404040;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  box-shadow: 1px 3px 5px 5px black;
  opacity: 0.8;
  cursor: default;
  flex-wrap: wrap;
  transition: right 0.25s ease;
}

.sidebar_container_right_in {
  right: 0px;
}

.legend_title {
  width: 100%;
  height: auto;
  align-self: center;
  color: white;
  font-weight: 700;
  font-size: 23px;
  opacity: 1;
  background-color: #485566;
  padding-left: 20px;
  padding-top: 3px;
  padding-bottom: 3px;
}

.legend_item {
  width: 80%;
  height: auto;
  align-self: center;
  color: white;
  margin-top: 10px;
  margin-left: auto;
  margin-right: auto;
  font-size: 1rem;
}

.color_item {
  display: inline;
  float: left;
  height: 25px;
  width: 25px;
  border-radius: 50%;
  border: 1px solid white;
}

.desc_item {
  display: inline;
  float: left;
  height: 25px;
  width: auto;
  text-align: center;
  vertical-align: middle;
  line-height: 25px;
  margin-left: 10px;
  font-size: 1rem;
}

.legend_desc {
  width: 90%;
  height: auto;
  align-self: flex-start;
  color: white;
  font-weight: 400;
  margin-top: 20px;
  margin-bottom: 10px;
  margin-left: 10px;
}
</style>